<template>
  <div class="card">
    <div class="card-header">Update관련 Hook</div>
    <div class="card-body">
      onBeforeUpdate, onUpdated
      <hr/>
      <p>count: {{count}}</p>
      <button @click="count++" class="btn btn-info btn-sm mr-2">증가</button>
      <button @click="count--" class="btn btn-info btn-sm">감소</button>
    </div>
  </div>
</template>

<script setup>
import { onBeforeUpdate, onUpdated, ref } from "vue";

const count = ref(0);

onBeforeUpdate(() => {
  console.log("onBeforeUpdate Hook");
});

onUpdated(() => {
  console.log("onUpdated Hook");
});
</script>

<style scoped></style>
