<template>
  <div class="card">
    <div class="card-header">Mount관련 Hook</div>
    <div class="card-body">
      onBeforeMount, onMounted, onBeforeUnmount, onUnmounted
    </div>
  </div>
</template>

<script setup>
import { onBeforeMount, onMounted, onBeforeUnmount, onUnmounted } from "vue";

onBeforeMount(() => {
  console.log("onBeforeMount Hook");
});

onMounted(() => {
  console.log("onMounted Hook");
});

onBeforeUnmount(() => {
  console.log("onBeforeUnmount Hook");
});

onUnmounted(() => {
  console.log("onUnmounted Hook");
});
</script>

<style scoped></style>
