<template>
  <div class="card">
    <div class="card-header">
      Exam03Ref
    </div>
    <div class="card-body">
      <div>
        <h6>[DOM 요소 참조]</h6>
        <p>data: {{data}}</p>
        <div  class="d-flex">
          <button v-on:click="setInputValue" class="btn btn-info btn-sm mr-2">값 변경</button>
          <input type="text" ref="inputRef" style="line-height:25px;"/>
        </div>
      </div>
      <hr/>      
      <div>
        <h6>[자식 컴포넌트 참조]</h6>
        <p>child data: {{(childRef!== null)?childRef.data:""}}</p>
        <p>
          <button v-on:click="changeChildData" class="btn btn-info btn-sm mr-2">자식 데이터 변경</button>
          <button v-on:click="callChildMethod" class="btn btn-info btn-sm mr-2">자식 메소드 호출</button>
        </p>
        <child ref="childRef" class="mt-3"></child>
      </div>
    </div>
  </div>
</template>

<script setup>
import Child from "./Child.vue";
import { ref, onMounted } from "vue";

const data = ref("summer");
const inputRef = ref(null);

onMounted(() => {
  inputRef.value.focus();
});

function setInputValue() {
  inputRef.value.value = data.value;
}

const childRef = ref(null);

function changeChildData() {
  childRef.value.data += 1; 
}

function callChildMethod() {
  childRef.value.method(); 
}
</script>

