<template>
  <div class="card">
    <div class="card-header">Setup Hook</div>
    <div class="card-body">
      <p>prop1: {{prop1}}</p>
      <hr/>
      <p>count: {{count}}</p>
      <button @click="count++" class="btn btn-info btn-sm mr-2">증가</button>
      <button @click="addCount" class="btn btn-info btn-sm mr-2">증가</button>
      <hr/>
      <button @click="emitEvent" class="btn btn-info btn-sm mr-2">이벤트 발행</button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";

export default {
  //props 선언
  props: ["prop1"],

  //컴포넌트 이벤트 선언
  emits: ["component-event"],

  //Setup Hook
  //1.<script setup> 권장
  //2.setup() 메소드는 다음과 같은 환경에서 사용
  //  1)빌드 과정 없이 사용코저 할 때
  //  2)Options API 안에서 Composition-API 기반 코드와 통합할 경우
  setup(props, context) {
    console.log("setup() 실행");

    //props 내용
    console.log("props", props);

    //반응형 데이터 선언
    const count = ref(0);

    //이벤트 핸들러 선언
    function addCount() {
      //반응형 데이터 업데이트
      count.value++;
    }
    function emitEvent() {
      //컴포넌트 이벤트 발생
      context.emit("component-event");
    }

    //템플릿에서 사용하기 위한 리턴값
    return {
      count,
      addCount,
      emitEvent
    };
  }
}
</script>

<!--
<script setup>
import { defineProps, defineEmits, ref } from "vue";

//props 선언
const props = defineProps(["prop1"]);

//컴포넌트 이벤트 선언
const emit = defineEmits(["component-event"]);

//반응형 데이터 선언
const count = ref(0);

//이벤트 핸들러 선언
function addCount() {
  count.value++;
}
function emitEvent() {
  emit("component-event")
}
</script>
-->

